// React
import React from "react";

// Gatsby
import { Link } from "gatsby";

// Internal dependencies
import "./navbar-item.css";


export default class NavbarItem extends React.Component
{
    /* VISUALS */

    render()
    {
        return(
            
            <Link className="navbar-item" to={this.props.targetUrl}>
                {this.props.label}
            </Link>
        );
    }
}