// React
import React from "react";

// Internal dependencies
import Navbar from "../navbar/navbar";
import NavbarItem from "../navbar-item/navbar-item";

import "./header.css";


export default class Header extends React.Component
{
    /* VISUALS */

    render()
    {
        return(
            <header className="header">
                <Navbar>
                    <NavbarItem
                        label="About"
                        targetUrl="/about/"/>
                    <NavbarItem
                        label="Donate"
                        targetUrl="/donate/"/>
                    <NavbarItem
                        label="Contact" 
                        targetUrl="/contact/"/>
                    <NavbarItem
                        label="Join"
                        targetUrl="/join/"/>
                </Navbar>
            </header>
        );
    }
}